import yellow_logo from "../img/yellow_logo.png";
import ImageFadeIn from "react-image-fade-in";


const MainBody = () => {
    return (
        <div className="mainbody-container">
            <div className="mainbody-image">
                <div className="yellow_logo_container">
                    <ImageFadeIn className="yellow_logo" src={yellow_logo} alt="Yellow Mexi Chips Logo" opacityTransition={1.5}  />                
                    {/* <img className="yellow_logo" src={yellow_logo} alt="Yellow Mexi Chips Logo" /> */}
                </div>
            </div>
                <div className="button_container">    
                    {/* <button className="mainbody-button">MENU</button> */}
                </div>
        </div>
    )
}
export default MainBody;