import menu from "../img/menu.jpg";

const Menu = () => {
    return (
        <div className="menu-container" id="menu">
            <div className="menu-h1">
                <h1>- Menu -</h1>
            </div>
                
            <div className="menu-img">
                <img src={menu} alt="Menu" />
            </div>
        </div>
    )
}
export default Menu;