import image1 from "../img/IMG02765.jpg"
import image2 from "../img/IMG02798.jpg"
import image3 from "../img/IMG02789.jpg"
import image4 from "../img/IMG02796.jpg"
import image5 from "../img/IMG02818.jpg"
import image6 from "../img/IMG02795.jpg"

function Gallery() {
    return (
        <div className="gallery-container" id="gallery">
            <div className="gallery-h1">
                <h1>- Gallery -</h1>
            </div>
            <div className="img-container">    
                
                    
                    <div className="gallery-img">
                        <img src={image1} alt="Gallery" />
                    </div>
                    
                    <div className="gallery-img"> 
                        <img src={image2} alt="Gallery" />
                    </div>
                    
                    <div className="gallery-img">
                        <img src={image3} alt="Gallery" />
                    </div>

                    <div className="gallery-img">
                        <img src={image4} alt="Gallery" />
                    </div>
                    
                    <div className="gallery-img">
                        <img src={image5} alt="Gallery" />
                    </div>
                    
                    <div className="gallery-img">
                        <img src={image6} alt="Gallery" />
                    </div>
                    
                
            </div>
        </div>
    )
}
export default Gallery;